@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "DotGothic16", sans-serif;
  height: 100vh;
  transition: ease-in-out;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
/*
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
} */

button {
  font-family: "DotGothic16", sans-serif;
  font-weight: bold;
  background-color: white;
  font-size: 20px;
  border: none;
  margin: 5px;
}
button:hover {
  border-bottom: 1px solid black;
}

.Header {
  height: 21px;
  margin-bottom: 20px;
}

button:focus {
  outline: none;
}

@media only screen and (max-width: 500px) {
  button {
    font-family: "DotGothic16", sans-serif;
    font-weight: bold;
    background-color: white;
    font-size: 15px;
    border: none;
    margin: 5px;
  }
}

.Popup {
  color: white;
  position: fixed;
  background-color: #21211f;
  width: 90vw;
  height: 90vh;
  top: 10vh;
  left: 5vw;
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 5px;
}
.Popup > h2 {
  justify-self: flex-start;
  margin-top: 0;
}

.video-container > video:focus {
  outline: none;
}
.video-container {
  height: 58vh;
  width: 60vw;
  object-fit: contain;
}

.popupinfo > a:visited {
  color: white;
}
.popupinfo > a {
  color: white;
}
.popupinfo > p {
  width: 60vw;
}
.closeicon {
  /* text-align: end; */
  -webkit-align-self: flex-end;
          align-self: flex-end;
  background-color: #21211f;
  width: 20px;
  margin-right: 15px;
  margin-top: 15px;
  color: white;
}

@media only screen and (max-width: 500px) {
  .popupinfo > p {
    font-size: 0px;
  }
}

.Project_img {
  height: 180px;
  width: 240px;
}
.Project {
  height: 220px;
  width: 260px;
  margin: 20px;
}

.Project_name {
  height: 220px;
  width: 260px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.5rem;
  font-weight: bold;
}

.Projects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.loading-page {
  position: absolute;
  text-align: center;
  background-color: white;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.Publication {
  border-bottom: 1px solid lightgray;
  text-align: left;
  width: 45vw;
  margin: 30px auto;
  min-width: 370px;
  padding: 0 10px;
}
.Publication-cont {
  display: -webkit-flex;
  display: flex;
}
img {
  width: 20vw;
  object-fit: contain;
  margin-right: 30px;
}

.Publication > a {
  color: black;
  text-decoration: none;
}

.Publication > a:visited {
  color: black;
  text-decoration: none;
}

.Publication > a:hover {
  color: gray;
}

/* .Publications {
  margin: 20px;
  height: 81vh;
  overflow: scroll;
} */

.home {
  display: grid;
  place-items: center;
}
.profile {
  display: -webkit-flex;
  display: flex;
  width: 45vw;
  height: 70vh;
}
.profile > img {
  border-radius: 22px;
  margin: 15px auto;
  max-width: 300px;
  min-width: 200px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
footer > img {
  width: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 5px;
  opacity: 0.5;
}
footer > img:hover {
  opacity: 1;
}

.profile > p {
  margin-left: 20px;
  height: 50vh;
  text-align: left;
  /* margin: auto; */
}
footer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

@media only screen and (max-width: 1320px) {
  .profile {
    width: 50vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .profile > p {
    margin-left: 0px;
    text-align: center;
    font-size: small;
  }
  .Publication {
    font-size: small;
  }
  footer {
    display: none;
  }
}
@media only screen and (max-width: 400px) {
  .profile {
    width: 80vw;
  }
}
@media only screen and (max-height: 620px) {
  footer {
    display: none;
  }
}

.Landingpg {
  border-radius: 20px;
  display: -webkit-flex;
  display: flex;
  width: 50vw;
  /* margin: auto; */
  margin: 35px auto;
  border-radius: 22px;
  overflow: hidden;
  /* -webkit-transform: translateZ(0); */
  box-shadow: 0 19px 51px 0 rgba(0, 0, 0, 0.16), 0 14px 19px 0 rgba(0, 0, 0, 0.07);
}

@media only screen and (max-width: 400px) {
  .Landingpg {
    width: 90vw;
  }
}

